html,
body,
#root {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
}

#root {
  background-color: #fee;
  display: flex;
  justify-content: center;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  max-width: 600px;
}

#config {
  display: flex;
  flex-direction: column;
  padding: 4px;
  text-align: left;
}

#config span {
  margin-top: 16px;
}

#header {
  flex: 0 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  text-align: left;
  font-size: 24px;
  color: #311;
  font-weight: bold;
  padding: 8px;
  border-bottom: 4px solid #311;
}

#header span {
  flex: 1 1;
}

#header i {
  margin: 4px;
}

#flashcard {
  flex: 1 1;
  -webkit-perspective: 1000;
          perspective: 1000;
  padding: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#controls {
  flex: 0 0 auto;
  padding: 4px;
  display: flex;
  justify-content: stretch;
  align-items: center;
}

#stats {
  flex: 0 0 50px;
  background-color: #999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button {
  flex: 1 1;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  font-size: 120%;
  font-weight: bold;
  color: white;
  border-radius: 4px;
  border: none;
  background-color: #533;
  padding: 12px;
  margin: 4px;
  box-shadow: 0px 4px 8px -1px #333;
}

.button:active {
  background-color: #fdd;
}

.showFront,
.showBack {
  height: 100%;
  width: 100%;
  -webkit-transition: 0.7s;
  transition: 0.7s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff0;
}

.showFront {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}

.showFront {
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
}

.front,
.back {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 32px;
  text-align: center;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 5px 10px -1px #333;
}

.front {
  color: #311;
  z-index: 2;
  background: #eaa;
  font-size: 32px;
}

.back {
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
  color: #fdd;
  background: #533;
  font-size: 32px;
}

